:root {
    --background-color: rgb(8, 12, 26);
    --border-color: rgb(255, 255, 255, 0.1);
  }
  
  * {
    box-sizing: border-box;
  }
  
  body {
    background-color: var(--background-color);
    display: flex;
    flex-direction: column;
    height: 100vh;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  h1, h2, h3, a, p, span {
    color: #fff;
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
  }
  
  a {
    text-decoration: none;
  }
  
  nav {
    display: flex;
    width: 100%;
    border-bottom: 1px solid var(--border-color);
  }
  
  nav > .nav-section {
    align-items: center;
    justify-content: center;
    padding: 3rem 2rem;
    display: flex;
    gap: 1rem;
    border-left: 1px solid var(--border-color);  
  }
  
  #nav-logo-section {
    justify-content: flex-start;
  }
  
  #nav-logo-section,
  #nav-link-section {
    flex-basis: calc(100% / 3);
  }
  
  #nav-social-section,
  #nav-contact-section {
    flex-grow: 1;
  }
  
  #nav-logo-section > a > i {
    font-size: 2.5em;
  }
  
  #nav-link-section {
    gap: 6rem;
  }
  
  #nav-social-section {
    gap: 3rem;
  }